import { request } from '@/api/reques.js';
import { goWindow } from '@/api/navigate.js'  // 页面跳转
export default {
	props: {
		member_name: {
			type: String
		},
		headimg: {
			type: String
		},
		bail_money:{
			type:Number
		},
		member_index_data: {
			type: Object,
			// 对象或数组默认值必须从一个工厂函数获取
			default: function() {
				return {};
			}
		},
		id_num:{
			type:String,
		},
		real_name:{
			type:String,
		}
	},

	data() {
		return {
			nodes: [
				{
					name: 'div',
					attrs: {
						class: 'div-class',
						style: ''
					},
					children: [
						{
							type: 'text',
							text: '未实名认证'
						}
					]
				}
			],
			mark:false,
		};
	},
	created() {
		//this.realName();
	},

	methods: {
		realName() {
			let _this = this;
			request(
				'/member/isBindId',
				{
					token: uni.getStorageSync('token')
				},
				'POST'
			).then(res => {
				if (res.data.data === 1) {
					_this.nodes[0].children[0].text = '已实名认证';
				}
			});
		},
		handleName(){
			let _this = this;
			request("/member/isBindId",{
				token:uni.getStorageSync('token')
			},"POST")
			.then((res)=>{
				if(res.data.data > 0){
					//_this.$c.msg("您已实名认证");
					_this.mark = true;
				}else{
					goWindow("/other_pages/RealName/RealName")
				}
			})
		},
		handleErr(e){
			if(e.type == "error"){
				this.headimg = "../../../static/images/toux.png"
			}
		},
		goWindow(url){
			let toKen = uni.getStorageSync('token');
			if(toKen !== ""){
				goWindow(url)
			}else{
				this.$c.msg("请先登录")
			}
			
		},
		handleLogin(){
			goWindow("/pages/common/login")
		},
		// 关闭弹窗
		handlerMark(){
			this.mark = false;
		}
	}
};
